<template>
	<view class="bg">
		<navbar v-model="screenHeight" :iSimmersive="false" :placeholder="true" title="正在与客服聊天中..."></navbar>
		<!-- 需配置bottom的偏移量, 用于底部留白 -->
		<mescroll-body 
		ref="mescrollRef" 
		bottom="120rpx" 
		@init="mescrollInit" 
		:down="downOption" 
		@down="downCallback" 
		:up="upOption"
		:height="screenHeight+'px'"
		>
			<!-- 无更多消息 -->
			<view v-if="isEnd" class="msg-end">没有更多消息了</view>
			
			<view class="message" v-for="msg in msgList" :key="msg.id" :id="msg.VIEW_ID" :class="msg.sClass">
				<!-- view class="message-time">2021-05-25</view -->
				<view class="message-box">
					<view class="avatar"><image v-if="msg.avatar" :src="msg.avatar"></image></view>
					<view class="content" v-if="msg.type=='text'">{{msg.content}}</view>
					<view class="content content-image" v-if="msg.type=='image'"><image :src="msg.content"></image></view>
					<view class="goods" v-if="msg.type=='goods'" @click="onGoodsJump(msg)">
						<view class="goods-photo">
							<image :src="msg.goods.photo"></image>
						</view>
						<view class="goods-name">{{msg.goods.goods_name}}</view>
						<view class="goods-store-nums">
							<text>库存：{{msg.goods.store_nums}}</text>
							<text>销量：{{msg.goods.sale}}</text>
						</view>
						<view class="goods-price">￥{{msg.goods.sell_price}}</view>
					</view>
				</view>
			</view>
		</mescroll-body>
		
		<view class="send-wrap">
			<view class="send-box">
				<view class="iconfont upload-image" @click="upload()">&#xe6da;</view>
				<view class="send-textarea">
					<input type="text" placeholder="请输入要发送的内容" v-model="content" cursor-spacing="10" />
				</view>
				<view class="iconfont send-btn" @click="send()">&#xe62d;</view>
			</view>
		</view>
		
	</view>
</template>

<script>
	import MescrollMixin from "@/uni_modules/mescroll-uni/components/mescroll-uni/mescroll-mixins.js";
	import navbar from "@/components/navbar/navbar";
	import config from "@/config";
	export default {
		mixins: [MescrollMixin],
		components: {
			navbar
		},
		data(){
			return {
				screenHeight: 0,
				content: "",
				downOption:{
					autoShowLoading: true, // 显示下拉刷新的进度条
					textColor: "#666" // 下拉刷新的文本颜色
				},
				upOption: {
					use: false, // 禁止上拉
					toTop: {
						src: '' // 不显示回到顶部按钮
					}
				},
				pageNum: 1, // 页码
				pageSize: 10, // 页长
				isEnd: false, // 是否无消息
				msgList: [],
				kf_id: 0
			};
		},
		onLoad(options) {
			if(options.id != undefined){
				this.kf_id = options.id;
				this.$http.updateMessageInfo({ kf_id: this.kf_id });
			}
			
			if(this.kf_id > 0 && this.$websocket.isConnect()){
				this.$websocket.send({ type: "kf", "kf_id": this.kf_id });
			}else{
				this.$websocket.connect(this.kf_id);
			}
			
			this.$websocket.message((res)=>{
				let obj = JSON.parse(res.data);
				if(obj == undefined){
					return;
				}
				
				switch(obj.type){
					case "ping":
						this.$websocket.send('{"type":"pong"}');
						break;
					case "bind":
						if(obj.status == 0){
							this.$utils.navigateTo("public/login");
						}
						break;
					case "say":
					case "goods":
						this.say(obj.data);
						let kf_id = obj.data.service_id != undefined && obj.data.service_id > 0 ? obj.data.service_id : this.kf_id;
						if(kf_id > 0){
							this.$http.updateMessageInfo({ kf_id: kf_id });
						}
						break;
					case "error":
						this.$utils.msg(obj.info);
						break;
				}
			});
		},
		onUnload() {
			this.$websocket.close();
		},
		methods:{
			upload(){
				let users = this.$storage.getJson("users");
				let that = this;
				uni.chooseImage({
					count: 1,
				    success: (chooseImageRes) => {
				        const tempFilePaths = chooseImageRes.tempFilePaths;
				        uni.uploadFile({
				            url: config.uni_app_web_api_url + '' + '/service/upload',
				            filePath: tempFilePaths[0],
				            name: 'file',
				            header: { "Auth-Token" : users.token },
							//formData: { "kf_id": this.kf_id },
				            success: (uploadFileRes) => {
								//let res = JSON.parse(uploadFileRes.data);
				                //that.avatar = res.data;
				            }
				        });
				    }
				});	
			},
			say(data){
				data.VIEW_ID = "msg"+data.id;
				this.msgList.push(data);
				this.$nextTick(()=>{
					let view = uni.createSelectorQuery().select('#' + data.VIEW_ID);
					view.boundingClientRect(v => {
						if(v != null){
							if(this.msgList.length <= 8){
								this.mescroll.scrollTo(v.top - 100, 0);
							}else{
								this.mescroll.scrollTo(Math.abs(v.top) + 99999, 0);
							}
						}
					}).exec();
				})
			},
			send(){
				if(this.content.length <= 0){
					this.$utils.msg("请输入内容！");
					return false;
				}
				if(this.$websocket.isConnect()){
					this.$websocket.send({
						"type": "say","content": this.content
					}).then(res=>{
						this.content = "";
					}).catch(err=>{
						this.$utils.msg("发送失败，请稍后在试");
					});
				}else{
					uni.hideLoading()
					uni.showModal({
						content: '与服务器断开连接，请稍后再试',
						showCancel: false
					});
				}
			},
			onGoodsJump(data){
				this.$utils.navigateTo("goods/view",{ id: data.goods_id });
			},
			/*下拉刷新的回调 */
			downCallback() {
				this.$http.getMessageList({ 
					page: this.msgList[0] != undefined ? this.msgList[0].id : 0,
					kf_id: this.kf_id
				 }).then((res)=>{
					// 先隐藏下拉刷新的状态
					this.mescroll.endSuccess();
					
					if(res.status == "-1"){
						this.isEnd = true; // 标记已无更多消息
						this.mescroll.lockDownScroll(true); // 锁定下拉
					}
					
					if(res.data.length < 10){
						this.isEnd = true; // 标记已无更多消息
						this.mescroll.lockDownScroll(true); // 锁定下拉
					}
					
					// 生成VIEW_ID,大写,避免污染源数据
					res.data.forEach(val=>{
						val.VIEW_ID = "msg" + val.id // 不以数字开头
					});
					
					let topMsg = this.msgList[0];
					this.msgList = res.data.concat(this.msgList);
					
					this.$nextTick(()=>{
						if(this.msgList.length <= 10){
							// 第一页直接滚动到底部 ( this.pageNum已在前面加1 )
							this.mescroll.scrollTo(99999, 0)
						}else if(topMsg){
							// 保持顶部消息的位置
							let view = uni.createSelectorQuery().select('#'+topMsg.VIEW_ID);
							view.boundingClientRect(v => {
								if(v != null)
								this.mescroll.scrollTo(v.top - 100, 0) // 减去上偏移量100
							}).exec();
						}
					})
					
				});
			}
		}
	}
</script>

<style lang="scss">
	.bg { background-color: #eee; }
	.msg-end { font-size: 28rpx; color: #666; text-align: center; padding: 20rpx 0; }
	.message{
		margin-top: 30rpx;
		.message-time { text-align: center; font-size:28rpx; color: #909090; height:40rpx; line-height: 40rpx; width: 100%; float:left; }
		.message-box {
			width: 100%;
			float: left; 
			margin-bottom: 65rpx;
			.avatar {
				width: 80rpx; height: 80rpx; border-radius: 50rpx; 
				background-color: #fff;
				image { width: 80rpx; height: 80rpx; border-radius: 50rpx;  }
			}
			.content {
				padding: 20rpx; border-radius: 40rpx;
				max-width: 350rpx; min-width: 100rpx; height: auto !important; line-height: 45rpx; height: 45rpx; min-height: 45rpx;
				font-size: 24rpx;
			}
			.content-image {
				image {
					max-width: 200rpx;
					max-height: 200rpx;
					min-width: 100rpx;
					min-height: 100rpx;
					border-radius: 20rpx;
				}
			}
			.goods { 
				padding: 20rpx; border-radius: 40rpx;
				background-color: #fff; width: 420rpx;
				.goods-photo {
					width: 150rpx; height: 150rpx; float: left;
					image {
						width: 150rpx; height: 150rpx; border-radius: 20rpx;
					}
				}
				.goods-name { 
					font-size: 24rpx; color:#666; width: 255rpx; 
					float: left; margin-left: 10rpx; margin-top: 10rpx;
					display: -webkit-box;overflow: hidden;-webkit-line-clamp: 2;-webkit-box-orient: vertical;
				}
				.goods-store-nums { 
					font-size: 24rpx; margin-left: 10rpx; float: left;
					text {
						color: #666;
						&:first-child {  }
						&:last-child { padding-left: 10rpx; }
					}
				}
				.goods-price { 
					width: 40%; float: left;
					font-size: 24rpx; color:#fe0000; margin-left: 10rpx;
				}
			}
		}
		&.l {
			.avatar { float: left; margin-left: 20rpx; }
			.content { margin-left: 20rpx; color: #fff; background-color: #07a4fd; float:left; }
			.content-image { background-color: transparent; }
			.goods { margin-left: 20rpx; float:left; }
		}
		&.r {
			.avatar { float: right; margin-right: 20rpx; }
			.content { margin-right: 20rpx; color: #909090; background-color: #fff; float:right }
			.content-image { background-color: transparent; }
			.goods { margin-right: 20rpx; float:right; }
		}
	}
	
	.send-wrap {
		position: fixed; bottom: 0; left: 0; box-sizing: border-box;
		width: 100%; height: 110rpx; background-color: #f5f5f5;
		.send-box {
			.upload-image { 
				float: left;
				width: 75rpx; height: 75rpx; 
				line-height: 75rpx; background-color: #fff;
				border-radius: 50rpx; text-align: center;
				margin-top: 18rpx; margin-left: 15rpx;
			}
			.send-textarea {
				width: 525rpx; float:left; margin-top: 18rpx;
				margin-left: 25rpx;
				input {
					text-indent: 15rpx; font-size:29rpx; color:#333;
					height: 75rpx; line-height: 75rpx;
					border: 0px solid #fff; background-color: #fff;
					border-radius: 50rpx;
				}
				[placeholder] { color:#c6c6c6; }
			}
			.send-btn {
				float: right;
				width: 75rpx; height: 75rpx; 
				line-height: 75rpx; background-color: #fff;
